    font-size: 16px;
    line-height: 24px;
  }
}
.page__body {
  min-height: 100%;
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
